@import url('https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@200;300&family=League+Spartan&display=swap');


.container {
  padding-top: 64px;
  padding-bottom: 64px;
  position: relative;
  text-align: center;
}

.hide {
  width: 15%;
  z-index: -1;
  position: absolute;
  left: 70%;
  /*bottom: 37%;*/
  bottom: 32%;
  transform: rotate(20deg);
  transition: all 1s ease;
}

.ea_img {
  width: 70%;
}

.header_text {
  position: absolute;
  top: 44%;
  left:58%;
  transform: translate(-50%, -50%);
}

h1 {
  text-align: left;
  margin:auto;
  font-family: League Spartan;
  color: #9451B9;
  font-size: 4.5vw;
}

#hobo:hover +#mc {
  bottom: 63%;
}

@media screen and (min-width: 601px) {
  #hobo:hover +#mc {
    bottom: 63%;
  }
  
}

@media screen and (max-width: 600px) {
  #hobo:hover +#mc {
    bottom: 58%;
  }
  .hide {
    bottom: 39%;
  }
  
}