
.about__container {
    padding-top: 120px;
    width: 80%;
    margin: auto;
    display: block;
    font-size: 2vw;
    font-family: Kumbh Sans;
    color: #fff;
}


@media screen and (min-width: 601px) {
    .about__container h1 {
        font-size: 120px;
    }
    .hr1 {
        width: 25%;
        position: absolute;
    }
    p {
        font-size: 32px;
    }
  }

@media screen and (max-width: 600px) {
    .about__container h1 {
        font-size: 36px;
    }
    .hr1 {
        width: 40%;
        position: absolute;
    }
    p {
        font-size: 24px;
    }
}