body {
  margin: 0;
  background-image: url(./images/background.png);
}
*{
  -ms-overflow-style: none;
}
::-webkit-scrollbar {
  display: none;
}

html {
  scroll-behavior: smooth; 
}

@media screen and (min-width: 601px) {
  h1 {
      font-size: 80px;
  }
  p {
    font-size: 30px
  }
}

@media screen and (max-width: 600px) {
  h1 {
      font-size: 60px;
    }
    p {
      font-size: 15px
    }
}